import css from "../../build/styles/core.css";
var uilibrary = "https://unpkg.com/mithril/mithril.min.js";
import mithril from "file-loader?name=mithril_[hash:4].js!mithril";

//import swURL from "file-loader?name=sw_[hash:6].js!../sw.js";

if ('serviceWorker' in navigator) {
//    navigator.serviceWorker.register("sw.js",{scope: './'});
  }

var main_element = document.getElementById('loading');
main_element.innerHTML = loader("loading UI library");

// add CSS file
var link = document.createElement('link');
link.rel = "stylesheet";
link.href = css;
link.onerror = function(){
    main_element.innerHTML = "Failed to load UI library, maybe network problem";
    throw new Error("Could not load css");
    };
document.head.appendChild(link);

// add mithril
var script = document.createElement('script');
script.src = uilibrary;
script.async = false;
script.onload = function(){
    init();
    };
script.onerror = function(){
    main_element.innerHTML = "Failed to load UI library, maybe network problem";
    throw new Error("Could not load UI library");
    };
document.head.appendChild(script);

// function to be executed after basic libraries are ready
function init(){
    main_element.innerHTML = loader("loading web atima library");
    import(/* webpackChunkName: "index" */ "./index.js").then(function(r){

        console.log("index loaded");    
    }).catch(function(e){
        main_element.innerHTML = "Failed to load web atima library, maybe network problem";
        throw new Error("Could not load webatima library");
        });
    }



